import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";
import Drawer from "@material-ui/core/Drawer";
import CssBaseline from "@material-ui/core/CssBaseline";
import Toolbar from "@material-ui/core/Toolbar";
import List from "@material-ui/core/List";
import AppBar from "@material-ui/core/AppBar";
import Typography from "@material-ui/core/Typography";
import photo from "../Resources/photo.JPG";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import Avatar from "@material-ui/core/Avatar";
import Grid from "@material-ui/core/Grid";
// import ListItemIcon from "@material-ui/core/ListItemIcon";
// import ListItemText from "@material-ui/core/ListItemText";
// import InboxIcon from "@material-ui/icons/MoveToInbox";
// import MailIcon from "@material-ui/icons/Mail";

const drawerWidth = 240;

const styles = theme => ({
  root: {
    display: "flex"
  },
  avatar: {
    margin: 10,
    width: 60,
    height: 60
  },
  bigAvatar: {
    margin: 10,
    width: 80,
    height: 80
  },
  appBar: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0
  },
  drawerPaper: {
    width: drawerWidth
  },
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.default,
    padding: theme.spacing.unit * 3
  }
});

function PermanentDrawerLeft(props) {
  const { classes } = props;

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <Grid container justify="left" alignItems="center">
            <Avatar alt="PK" src={photo} className={classes.Avatar} />
            <Typography variant="h6" color="inherit" noWrap>
              Praveen Kumar Thondapu
            </Typography>
          </Grid>
        </Toolbar>
      </AppBar>
      <Drawer
        className={classes.drawer}
        variant="permanent"
        classes={{
          paper: classes.drawerPaper
        }}
        anchor="left"
      >
        <div className={classes.toolbar} />
        <Divider />
        <span className="bg-info " justify="center" alignItems="center">
          <Avatar alt="Remy Sharp" src={photo} className={classes.bigAvatar} />
        </span>
        <List className="bg-info font-italic" button>
          <ListItem button key>
            <Link className="nav-item nav-link text-dark" to="../">
              Home
            </Link>
          </ListItem>
          <Divider />
          <ListItem button key>
            <Link className="nav-item nav-link text-dark" to="../profile">
              Profile
            </Link>
          </ListItem>
          <Divider />
          <ListItem button key>
            <Link className="nav-item nav-link text-dark" to="../education">
              Education Details
            </Link>
          </ListItem>
          <Divider />
          <ListItem button key>
            <Link className="nav-item nav-link text-dark" to="../projects">
              Projects
            </Link>
          </ListItem>
          <Divider />
          <ListItem button key>
            <Link className="nav-item nav-link text-dark" to="../other">
              Other
            </Link>
          </ListItem>
        </List>
      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
      </main>
    </div>
  );
}

PermanentDrawerLeft.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(PermanentDrawerLeft);
