import React, { Component } from "react";
import Projects from "./projects";
import Education from "./education";
import Other from "./other";
import Profile from "./profile";
class Home extends Component {
  render() {
    return (
      <React.Fragment>
        <span class="border ">
          <Profile />
          <Education />
          <Projects />
          <Other />
        </span>
      </React.Fragment>
    );
  }
}

export default Home;
